import { Controller, Command } from '@ali/4ever-cangjie';
import isSelectionInQuickInsert from '../queries/isSelectionInQuickInsert';
import { isInsertInline } from '@ali/4ever-utils';
import remove from '../commands/remove';

export default function onCommand(
  command: Command,
  controller: Controller,
  next: () => void,
) {
  if (isInsertInline(command) && isSelectionInQuickInsert(controller)) {
    controller.command(remove);
  }
  next();
}
